import React from 'react';
import styles from './AboutMain.module.scss'
import PictureSlider from "../../PictureSlider/PictureSlider";

function AboutMain(props) {
    return (
        <section className={styles['about-main']}>
            <div className={styles['about-main__wrapper']}>
                <h2 className={styles['about-main__title']}>  </h2>
                <div className={styles['about-main__text-container']}>
                    <p className={styles['about-main__text-block']}>Art Memory   ,   ,    .        ,     . </p>
                    <p className={styles['about-main__text-block']}>           .    -   -   ,     ,    .</p>
                </div>
                <h2 className={styles['about-main__about-title']}> Standart</h2>
                <p className={styles['about-main__about-text']}>  1-2 </p>
                <PictureSlider />
                <h2 className={`${styles[`about-main__about-title`]} ${styles[`about-main__about-title_two`]}`}> premium</h2>
                <p className={styles['about-main__about-text']}>   3- </p>
                <PictureSlider styleProp={'_left'}/>
            </div>
        </section>
    );
}

export default AboutMain;